import {Dialog} from '../../gollum-dialog'
import {replaceSelection} from '../gollum-editor'

export const Textile = {
  'function-bold': {
    search: /(^[\n]+)([\n\s]*)/g,
    replace: '*$1*$2',
  },

  'function-italic': {
    search: /(^[\n]+)([\n\s]*)/g,
    replace: '_$1_$2',
  },

  'function-hr': {
    append: '\n***\n',
  },

  'function-code': {
    search: /(^[\n]+)([\n\s]*)/g,
    // eslint-disable-next-line github/unescaped-html-literal
    replace: '<pre><code>$1</code></pre>$2',
  },

  'function-ul': {
    search: /(.+)([\n]?)/gi,
    replace: '* $1$2',
  },

  'function-ol': {
    search: /(.+)([\n]?)/gi,
    replace: '# $1$2',
  },

  'function-blockquote': {
    search: /(.+)([\n]?)/gi,
    replace: 'bq. $1$2',
  },

  'function-link': {
    exec() {
      Dialog.init({
        title: 'Insert Link',
        fields: [
          {
            id: 'text',
            name: 'Link Text',
            type: 'text',
            help: 'The text to display to the user.',
          },
          {
            id: 'href',
            name: 'URL',
            type: 'text',
            help: 'The URL to link to.',
          },
        ],
        OK(res) {
          let h = ''
          if (res['text'] && res['href']) {
            h = `"${res['text']}":${res['href']}`
          }
          replaceSelection(h)
        },
      })
    },
  },

  'function-image': {
    exec() {
      Dialog.init({
        title: 'Insert Image',
        fields: [
          {
            id: 'url',
            name: 'Image URL',
            type: 'text',
          },
          {
            id: 'alt',
            name: 'Alt Text',
            type: 'text',
          },
        ],
        OK(res) {
          if (res['url']) {
            let h = `!${res['url']}`
            if (res['alt'] !== '') {
              h += `(${res['alt']})`
            }
            h += '!'
            replaceSelection(h)
          }
        },
      })
    },
  },
}

